.main__carousel {
  overflow: hidden;
  width: 600px;
  height: 780px;
  position: relative;
}

@media screen and (min-width: 1024px) and (max-width: 1720px) {
  .main__carousel {
    width: 400px;
    height: 480px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .main__carousel {
    width: 260px;
    height: 352px;
  }
}

@media screen and (min-width: 320px) and (max-width: 768px) {
  .main__carousel {
    width: 100%;
    height: 340px;
  }
}