.main__img {
  width: 600px;
  height: 686px;
  object-fit: cover;
  position: relative;
  margin: 19px 0 0;
}

@media screen and (min-width: 1024px) and (max-width: 1720px) {
  .main__img {
    width: 400px;
    height: 380px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .main__img {
    width: 260px;
    height: 290px;
  }
}

@media screen and (min-width: 320px) and (max-width: 768px) {
  .main__img {
    width: 100%;
    height: 340px;
    margin: 0;
  }
}